@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,600;0,700;1,400;1,600&family=Oswald:wght@200;300;400;500&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

#root,
body {
  height: 100vh;
  width: 100%;
}

.App {
  height: 100%;
  width: 100%;
  text-align: center;

  display: flex;
  flex-direction: column;
  align-items: center;

  background-image: url(/indie-com-farofa/static/media/colorfull-logo.f62cd7ad.jpg);
  background-repeat: no-repeat;
  background-position: 0 0;
  background-position: initial;
  font-family: "Open Sans";
}

.AppHeader{height:155px;width:100%;display:grid;grid-template-rows:75% 25%}.AppHeader .HeaderMedia{height:100%;width:100%;display:flex;align-items:center;background-color:#342c24}.AppHeader .HeaderMedia .IndieComFarofaPresent{margin-left:80px;height:100%;width:220px;display:flex;align-items:center;justify-content:space-between;color:#fff}.AppHeader .HeaderMedia .IndieComFarofaPresent .IndieComFarofaLogo{width:40px;height:40px;box-shadow:2px 3px 4px #00000025}.AppHeader .HeaderMedia .IndieComFarofaPresent .IndieComFarofaLogo img{width:100%;height:100%;-webkit-filter:brightness(1.1) contrast(1.2);filter:brightness(1.1) contrast(1.2)}.AppHeader .HeaderMedia .IndieComFarofaPresent .IndieComFarofaText{display:flex;flex-direction:column;align-items:flex-start}.AppHeader .HeaderMedia .IndieComFarofaPresent .IndieComFarofaText h1{margin-bottom:2px;font-size:1.4rem;font-family:"Oswald",sans-serif;text-transform:uppercase;font-weight:500}.AppHeader .HeaderMedia .IndieComFarofaPresent .IndieComFarofaText h2{font-size:0.95rem;font-family:"Open Sans",sans-serif;font-weight:400;color:#d1c89e}.AppHeader .HeaderContent{height:100%;width:100%;display:flex;justify-content:space-between;align-items:center;background-color:#fff;box-shadow:2px 3px 4px #00000025}.AppHeader .HeaderContent .HeaderNavigation{width:40%;display:flex}.AppHeader .HeaderContent .HeaderNavigation ul{margin:90px;height:100%;width:100%;display:flex;align-items:center}.AppHeader .HeaderContent .HeaderNavigation ul .NavLink{height:100%;margin:0;margin-right:10px;list-style:none;display:flex;justify-content:center;align-items:center}.AppHeader .HeaderContent .HeaderNavigation ul .NavLink a{padding:4px;height:100%;color:#342c24;text-decoration:none;font-size:0.95rem;transition:all 0.25s ease-out}.AppHeader .HeaderContent .HeaderNavigation ul .NavLink a.active,.AppHeader .HeaderContent .HeaderNavigation ul .NavLink a:hover{background-color:#342c24;color:#fff}.AppHeader .HeaderContent .SocialMedia{margin-right:80px;width:-webkit-max-content;width:-moz-max-content;width:max-content;display:flex;align-items:center}.AppHeader .HeaderContent .SocialMedia .SocialMediaIcons{margin-left:10px;padding:2px;display:flex;align-items:center;border-radius:100px;transition:all 0.25s ease-out}.AppHeader .HeaderContent .SocialMedia .SocialMediaIcons:hover{background-color:#d1c89e}.AppHeader .HeaderContent .SocialMedia .SocialEmail{text-decoration:none;font-size:0.95rem;font-style:italic;color:#342c24;transition:all 0.25s ease-out}.AppHeader .HeaderContent .SocialMedia .SocialEmail:hover{text-decoration:underline}

